* {
  font-family: "Montserrat", sans-serif;
  margin: 0;
}

.App {
  text-align: center;
}

#body {
  font-size: 20px;
    background: linear-gradient(to bottom, #1ab7bc, #0aa7ac);
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    overflow-x: hidden;
}

 /* Split the screen in half */
 .split {
  height: 100vh;
  width: 50%;
  position: fixed;
  top: 0;
  overflow-x: hidden;
}

/* Control the left side */
.left {
  left: 0;
  background: linear-gradient(to bottom, #1ab7bc, #0aa7ac);
}

/* Control the right side */
.right {
  right: 0;
  background: linear-gradient(to bottom, #fff, #eaeaea);
}

/* If you want the content centered horizontally and vertically */

.bottom {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bigbutton {
  display: block;
  width: 100%;
  padding: 24px 38px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
}

.bigtext {
  font-size: 35px;
  font-weight: 500;
}

.bigbutton {
  font-size: 30px !important;
}

.big-button {
    background: linear-gradient(to bottom, #1d7376, #0d6a6a);
    background-repeat:no-repeat;
    outline:none;
    border: #005356 1px solid;
    color: white;
    padding: 10px;
    padding-top: 7px;
    padding-bottom: 7px;
    font-size: 16px;
    text-align: center;
    border-radius: .45rem;
    cursor:pointer;
}

.big-button:hover {
  background: #0d6366;
}

.big-button:disabled {
  background: linear-gradient(to bottom, #cccccc, #aaaaaa);
  box-shadow: -1px 1px 3px 1px rgba(0, 0, 0, 0), -1px 1px 3px rgba(0, 0, 0, 0.3);
  outline:none;
  border: #888888 1px solid;
  opacity: 1.0; 
  cursor: not-allowed;
}

.small-button {
    background-color: #1d7376;
    background-repeat:no-repeat;
    outline:none;
    border: #005356 1px solid;
    color: white;
    padding: 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 12px;
    border-radius: 6px;
    margin: 5px;
    cursor:pointer;
}

.small-button:hover {
  background-color: #0d6366;
  outline:none;
}

.small-button:disabled {
  background: linear-gradient(to bottom, #eee, #ccc);
  box-shadow: none;
  outline:none;
  border: #aaa 1px solid;
  opacity: 1.0; 
  cursor: not-allowed;
  color: #888;
}

/* tooltips */

.tooltip {
  position: relative;
}

.tooltiptext {
  visibility: hidden;
  opacity: 0%;
  transition:opacity 0.2s;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  width: auto;
  white-space:nowrap;
  padding: 5px;
}

.tooltiptext {
  /* Position the tooltip */
  position: absolute;
  bottom: 120%;
  z-index: 10;
  overflow: hidden;
}

.tooltiptext-bottom {
  bottom: -140% !important;
  left: 70%;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 100%;
  transition:opacity 0.2s;
}

.button-svg-container {
  display: flex;
  flex-direction: row;
  font-weight: 500;
}


.svg-icon-button {
  width: 20px;
  height: 20px;
}


.svg-text-button {
  margin-top: 3px;
  padding-left: 15px;
  padding-right: 10px;
}
